#header {
    background: linear-gradient(rgba(195, 232, 255, 0.3) 0, rgba(195, 232, 255, 0.3) 0),
    url("../img/backgroundImage.jpg") no-repeat top;
    background-size: cover;

}
#header-wrap {
    width: 1280px;
    height: 400px;
    margin: 0 auto;
    box-sizing: border-box;
    /*background-color: #2e5656;*/
}
#header-wrap > #header-top {
    display: flex;
    justify-content: space-between;

}
