/* 로고 */
#header-top > #logo {
    padding: 2rem 1rem;
    color: rgba(255, 255, 255, 0.9);
}

/* 메뉴 */
#header-top > .header-menus {
    padding: 2rem 1rem
}
#header-top > .header-menus > ul {
    display: flex;
}
#header-top > .header-menus > ul > li {
    margin: 0 1.5rem;
    font-size: 1.1rem;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.9);
}
.header-menus-toggle {
    display: none;
    padding: 2rem 1rem;
}
.header-menus-toggle > .fa-bars{
    color: white;
    font-size: 1.5rem;
}

/* 메인 내용 */
#header-main > h1 {
    margin-top: 3rem;
    text-align: center;
    text-shadow: 3px 3px 3px #666;
    font-size: 3rem;
    color: rgb(255, 255, 255);
}
#header-main > div {
    margin: 2rem auto;
    width: 50rem;
    height: 4rem;
    background-color: rgba(255, 229, 183, 0.4);
    border-radius: 3rem;

    display: flex;
    justify-content: center;
    align-items: center;
}
#header-main > div > span {
    font-size: 1.5rem;
    color: #444;
}

@media only screen and (max-width: 768px) {
    #header-wrap {
        width: 100%;
    }

    .header-menus-toggle {
        display: block;
    }
    .header-menus {
        display: none;
    }
}